.messageForm {
    position: relative;
    padding: 10px 0;
}

.messageForm > .underline {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: 0.35s;
    background-color: blue;
}

.messageForm > label {
    position: absolute;
    pointer-events: none;
    translate: 0 -50%;
    transition: 0.35s;
    top: 50%;
    left: 34px;
    white-space: nowrap;
}

.messageForm > .icon {
    position: absolute;
    pointer-events: none;
    translate: 0 -50%;
    transition: 0.35s;
    top:47%;
    left: 12px;
    white-space: nowrap;
}

.messageForm > input {
    height: 50px;
    width: 220px;
    padding-left: 35px;
    border: 0.5px solid grey;
    border-bottom: solid --var(border-colour);
    background: transparent;
    color: beige;
}

.messageForm >
:is(input:focus, input.has-value)
~ label {
    translate: -35px -48px;
    scale: 0.875;
    color: --var(border-colour);
}

.messageForm >
:is(input:focus, input.has-value)
~ .underline {
    border: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.messageForm > textarea {
    padding-left: 10px;
    border: 0.5px solid grey;
    border-bottom: solid --var(border-colour);
    background: transparent;
    width: 400px;
}

.messageForm >
:is(textarea:focus, textarea.has-value)
~ label {
    translate: -35px -135px;
    scale: 0.875;
    color: --var(border-colour);
}

.messageForm >
:is(textarea:focus, textarea.has-value)
~ .underline {
    border: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

